// fonts
$oswald: "Oswald", sans-serif;
$arialmt: "Arial MT", sans-serif;

//font-color
$main-font-color: #444444;
$title-font-color: #111111;
$label-font-color: #222222;
$nav-font-color: #222222;
$contrast-font-color: #ffffff;
$advantages-num-font-color: #f2f2f2;

// background-color
$background-color: #ffffff;
$add-background-color: #f2f2f2;
$icon-background-color: #666666;
$main-color: #68b738;
$button-hover-color: #5eaa2f;
$border-color: #cdcdcd;
$slider-background-color: #eaeaea;

// font-size
$table-font-size: 12px;
$table-font-size-tablet: 16px;
$main-font-size: 14px;
$label-font-size: 16px;
$card-title-font-size: 16px; //
$button-font-size: 16px;
$button-font-size-tablet: 20px;
$nav-mobile-size: 20px;
$page-title-size: 36px;
$card-title-size: 24px; //
$title-font-size-tablet: 60px;
$main-font-size-tablet: 16px;
$advantages-num-font-size: 281px;

// font-weight
$weight-regular: 400;
$weight-medium: 500;

// main-pading
$main-padding-mobile: 20px;
$main-padding-tablet: 30px;
$main-padding-desktop: 110px;

// display width
$mobile-width: 320px;
$mobile-width-only: 767px;
$tablet-width: 768px;
$desktop-width: 1440px;

//retina
$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;

// card-padding
$card-padding-tablet: 38px;
$card-padding-desktop: 38px;

// form-sizes
$radio-size: 30px;
$radio-size-tablet: 40px;

//$slider-background
$slider-background: calc(((100% - #{$desktop-width}) / 2) + 721px + (650px * 50 / 100)); //721px -    ; 650px -    ; 50 -  inputa
